<template>
  <div class="incharge-page">
    <van-nav-bar
      fixed
      border
      title="积分充值"
      left-arrow
      @click-left="goBack"
    />
    <div class="score-box">
      <div>当前可用积分</div>
      <div class="score">{{ userInfo.integral }}</div>
    </div>
    <div class="intro cell-group">
      <h2>积分免费申请规则</h2>
      <p>1、点击申请后等待后台审核,审核通过后可获得50积分;</p>
      <p>2、管理员将根据平台使用频率和需求发布、需求解决数量作为审核依据;</p>
      <p>3、如有疑问，可联系平台工作人员：范秘书，15602908103（微信同号）</p>
      <p>4、本规则最终解释权归平台所有。</p>
    </div>

    <van-row class="bottom-bar cell-group">
      <van-button @click="goBack" square block>取消</van-button>
      <van-button block square type="danger" @click="handleSubmit"
        >申请</van-button
      >
    </van-row>
  </div>
</template>
<script>
import { mapGetters, mapState } from "vuex";
import { Button, Row, Col, NavBar } from "vant";
import { inCharge } from "../api/base";
export default {
  name: "about",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [NavBar.name]: NavBar,
    [Button.name]: Button
  },
  data() {
    return {
      loading: false
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"])
  },
  methods: {
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },
    async handleSubmit() {
      if (this.loading) return;

      this.loading = true;
      try {
        await inCharge(this.userInfo.id);
        this.loading = false;
        this.$dialog
          .alert({
            title: "温馨提示",
            message: "申请成功，我们将尽快审核",
            confirmButtonText: "确认"
          })
          .then(() => {
            this.goBack();
          });
      } catch (e) {
        this.loading = false;
        console.log(e);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.score-box {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #c11823;
  color: #fff;
  flex-direction: column;

  .score {
    margin-top: 10px;
    font-size: 32px;
  }
}

.cell-group {
  margin-top: 12px;
}

.intro {
  padding: 10px 15px;
  background-color: #fff;

  h2 {
    font-size: 18px;
    color: #000;
  }

  p {
    font-size: 12px;
    color: #666666;
    margin-bottom: 1em;
  }
}

.incharge-page {
  padding-bottom: 45px;
}

.bottom-bar {
  position: absolute;
  display: flex;
  bottom: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 750px) {
  .score-box {
    height: 150px;

    .score {
      margin-top: 10px;
      font-size: 32px;
    }
  }

  .cell-group {
    margin-top: 12px;
  }

  .intro {
    padding: 10px 15px;

    h2 {
      font-size: 18px;
    }

    p {
      font-size: 12px;
    }
  }

  .incharge-page {
    padding-bottom: 45px;
  }
}
</style>
